.container_media{
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 2rem;
  background-color: #1C1C1C;
  border-radius: 5px;
  position: absolute;
  bottom: 0;
  
  }
  .container_media p{
    font-size: 1.2rem;
    color:  #fff ;
    margin-left: 20px;
  }
  .container_media a{
    text-decoration: none;
    color: #fff;
  } 
  
  .container_media ul{  
    display:flex;
    margin-right: 50px;
  }
  
  .container_media li{  
    list-style: none;
    margin: 0 15px;
    gap: 20px;
  }
  
